"use client";

import { buttonVariants } from "../ui/button";
import Link from "next/link";
import { RouteLinks } from "./utils/route-links";
import { useEffect, useState } from "react";
import { usePathname } from "next/navigation";

export const Header = () => {
  const [isSignedIn, setIsSignedIn] = useState(false);

  const checkUserSignIn = async () => {
    const session = await fetch(`/api/auth`).then((res) => res.json());
    if (session) {
      setIsSignedIn(true);
      console.log(isSignedIn);
    } else {
      setIsSignedIn(false);
      console.log(isSignedIn);
    }
  };

  useEffect(() => {
    checkUserSignIn();
  }, []);

  const isNotRoot = usePathname() !== "/";

  return (
    <div className="fixed flex h-14 w-[100%] items-center justify-center gap-2 whitespace-nowrap border-b font-geistmono text-sm backdrop-blur">
      <RouteLinks />
      <Link
        className={`${buttonVariants({ variant: "default" })}`}
        href="/route/overview"
      >
        {isSignedIn ? "Sign out" : "Sign in"}
      </Link>
    </div>
  );
};
